@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #444444;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 50px;
  }
}

.button {
  display: flex;
  justify-content: center;
  text-decoration: none;
  font-family: "Open Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  align-items: center;
  text-transform: uppercase;
  color: #444444;
  border: none;
  background-color: #ffffff;
  outline: 2px solid #444444;
  cursor: pointer;
}

.button:hover {
  background-color: #444444;
  color: #ffffff;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
}

.button:focus-visible {
  background-color: #444444;
  color: #ffffff;
}

.button:invalid {
  background-color: #ececec;
  color: #ffffff;
}

.header {
  min-height: 79px;
  min-width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .header {
    align-items: center;
  }
  .header::before {
    position: absolute;
    content: "";
    width: 100%;
    left: 0;
    right: 0;
    height: 100%;
    min-height: 62px;
    background-color: #f9f9f9;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    z-index: -1;
  }
}

.header__wrapper {
  width: 100%;
  min-width: 320px;
  max-width: 1150px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__wrapper {
    display: flex;
    align-items: center;
    min-width: 768px;
    flex-wrap: wrap;
    min-height: 89px;
  }
}

.header__logo {
  z-index: 1;
  width: 100%;
  min-height: 79px;
}
@media (min-width: 768px) {
  .header__logo {
    display: flex;
    width: max-content;
    margin: 0;
    padding-left: 50px;
  }
}

.header__nav {
  width: 100%;
}

.header__text {
  display: none;
}
@media (min-width: 768px) {
  .header__text {
    display: flex;
    order: 5;
    width: 40%;
    align-items: flex-start;
    text-align: end;
    justify-content: flex-end;
    margin: 0;
    padding-right: 50px;
    padding-top: 20px;
    z-index: 2;
  }
}
@media (min-width: 1150px) {
  .header__text {
    margin-left: 50px;
    width: 30%;
  }
}

.logo {
  pointer-events: none;
  text-decoration: none;
  position: absolute;
  top: 22px;
  left: calc(50% - 43px);
  z-index: 10;
}
.logo:hover {
  opacity: 0.6;
}
.logo:active {
  opacity: 0.3;
}
.logo:focus {
  opacity: 0.6;
  outline: none;
}
.logo:disabled {
  opacity: 0.2;
  outline: none;
}
@media (min-width: 768px) {
  .logo {
    width: 47%;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    min-height: 90px;
  }
}
@media (min-width: 1150px) {
  .logo {
    width: max-content;
    left: 50%;
    transform: translateX(-50%);
    padding: 0;
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

.logo__image,
.logo__footer {
  display: flex;
  pointer-events: all;
  cursor: pointer;
}

@media (min-width: 768px) {
  .logo__image {
    width: 114px;
    height: 46px;
    display: flex;
    align-items: center;
  }
}
@media (min-width: 1150px) {
  .logo__image {
    width: 149px;
    height: 72px;
  }
}

.logo__footer {
  display: none;
}
@media (min-width: 768px) {
  .logo__footer {
    display: flex;
  }
}

@media (min-width: 768px) {
  .logo__footer-icon {
    width: 102px;
    height: 34px;
  }
}

.nav {
  position: absolute;
  top: 0;
  left: 0;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    flex-direction: row;
    min-height: 89px;
    width: 100%;
  }
}

.nav--closed .nav__item {
  display: none;
}
@media (min-width: 768px) {
  .nav--closed .nav__item {
    display: flex;
  }
}

.nav--nojs {
  position: relative;
  margin-top: 79px;
}

.nav--nojs .logo {
  top: -56px;
}

.nav__toggle {
  display: block;
  position: absolute;
  cursor: pointer;
  border: none;
  top: 29px;
  right: 30px;
  left: auto;
  background-repeat: no-repeat;
  background-color: inherit;
  width: 21px;
  height: 20px;
  padding: 0;
  z-index: 3;
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:active {
  opacity: 0.3;
}
.nav__toggle:focus-visible {
  border: none;
  outline: 2px solid #b5b5b5;
  outline-offset: 2px;
  background-color: transparent;
}
.nav__toggle:disabled {
  opacity: 0.1;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav--closed .nav__toggle {
  background-image: url("../img/toggle-open-button.svg");
  background-position: 0 3px;
}

.nav--opened .nav__toggle {
  background-image: url("../img/toggle-close-button.svg");
  background-position: 0 0;
}

.nav--nojs .nav__toggle {
  display: none;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .nav__list {
    flex-direction: row;
    justify-content: start;
  }
}

.nav--nojs .nav__list {
  position: static;
  width: 100%;
}
@media (min-width: 768px) {
  .nav--nojs .nav__list {
    display: flex;
  }
}

.nav__item {
  display: flex;
  min-height: 79px;
  width: 100%;
  align-items: center;
  padding-left: 24%;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .nav__item {
    width: auto;
    padding: 0;
    box-shadow: none;
    min-height: 89px;
  }
}

.nav--nojs .nav__item {
  display: flex;
}

.nav__item a {
  color: #000000;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.site-list {
  width: 100%;
  position: relative;
  top: 79px;
  left: 0;
}
@media (min-width: 768px) {
  .site-list {
    width: 60%;
    order: 2;
    top: 0;
    padding-left: 50px;
    display: grid;
    grid-template-columns: 179px 162px;
    column-gap: 10px;
  }
}
@media (min-width: 1150px) {
  .site-list {
    order: 1;
    width: 40%;
    margin-right: auto;
    grid-template-columns: 167px 212px;
    column-gap: 0;
    z-index: 4;
  }
}

.site-list__item {
  z-index: 2;
  background-color: #ffffff;
}
.site-list__item :hover {
  opacity: 0.6;
}
.site-list__item :active {
  opacity: 0.3;
}
.site-list__item :focus-visible {
  outline: 2px solid #444444;
}
.site-list__item :invalid {
  background-color: #ececec;
}
@media (min-width: 768px) {
  .site-list__item {
    z-index: 2;
    background-color: inherit;
    width: 167px;
    display: flex;
    align-items: flex-start;
    height: 100%;
  }
}
@media (min-width: 1150px) {
  .site-list__item {
    width: 100%;
  }
}

@media (min-width: 1150px) {
  .site-list__item:nth-child(even) {
    padding-left: 35px;
    padding-right: 35px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.site-list__link {
  color: inherit;
  display: flex;
  position: relative;
  padding: 22px 0 22px 0;
}
@media (min-width: 768px) {
  .site-list__link {
    font-size: 15px;
    padding: 20px 0 20px 0;
  }
}
@media (min-width: 1150px) {
  .site-list__link {
    padding: 33px 0 20px 0;
  }
}

.site-list__link--current {
  cursor: default;
  pointer-events: none;
}

.user-list {
  width: 100%;
  position: relative;
  top: 79px;
}
@media (min-width: 768px) {
  .user-list {
    width: 53%;
    height: 100%;
    top: 0;
    margin-left: auto;
    z-index: 3;
    background-color: inherit;
    display: grid;
    grid-template-columns: 84px 1fr;
  }
  .user-list::before {
    position: absolute;
    content: "";
    width: 200vw;
    right: -100vw;
    height: 100%;
    background-color: #ffffff;
    border-bottom: 1px solid #ececec;
    z-index: -2;
  }
}
@media (min-width: 1150px) {
  .user-list {
    order: 2;
    width: 36%;
  }
}

.user-list__item {
  z-index: 2;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .user-list__item {
    padding: 0 50px 0 38px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    height: 100%;
    display: flex;
    align-items: flex-start;
    background-color: inherit;
  }
  .user-list__item--search {
    padding: 0 20px 0 20px;
  }
}

.user-list__link {
  color: inherit;
  display: flex;
  position: relative;
  padding: 22px 0 22px 0;
}
.user-list__link:hover .user-list__link-icon {
  fill: #63d1bb;
  color: #63d1bb;
  opacity: 1;
}
.user-list__link:hover .user-list__link-text {
  opacity: 0.6;
}
.user-list__link:active .user-list__link-icon {
  fill: #63d1bb;
  color: #63d1bb;
  opacity: 0.3;
}
.user-list__link:active .user-list__link-text {
  opacity: 0.3;
}
.user-list__link:focus-visible {
  outline: 2px solid #444444;
}
.user-list__link:invalid {
  background-color: #ececec;
}
@media (min-width: 768px) {
  .user-list__link {
    min-width: 211px;
    width: 100%;
    display: flex;
    padding: 37px 0 26px 0;
  }
  .user-list__link--search {
    min-width: 20%;
    justify-content: center;
  }
  .user-list__link--search .user-list__link-text {
    display: none;
  }
}

.user-list__link--current {
  cursor: default;
  pointer-events: none;
}

.user-list__link-icon {
  position: absolute;
  top: 25px;
  left: -46px;
  color: inherit;
  opacity: 0.2;
}
@media (min-width: 768px) {
  .user-list__link-icon {
    position: relative;
    left: 0;
    top: 0;
    display: inline-block;
  }
}

.user-list__link-text {
  display: inline-block;
  width: 100%;
}
@media (min-width: 768px) {
  .user-list__link-text {
    width: 211px;
    text-align: end;
    margin-left: auto;
  }
}

.site-additional-list {
  display: none;
}
@media (min-width: 1150px) {
  .site-additional-list {
    order: 3;
    display: flex;
    flex-wrap: wrap;
    width: 55%;
    padding-left: 50px;
    margin-right: auto;
    z-index: 3;
  }
}

@media (min-width: 1150px) {
  .site-additional-list__item {
    font-size: 15px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    min-height: 70px;
    width: max-content;
    max-width: 160px;
    margin-right: 46px;
  }
  .site-additional-list__item :hover {
    opacity: 0.6;
  }
  .site-additional-list__item :active {
    opacity: 0.3;
  }
  .site-additional-list__item :focus-visible {
    outline: 2px solid #444444;
  }
  .site-additional-list__item :invalid {
    background-color: #ececec;
  }
}

@media (min-width: 1150px) {
  .site-additional-list__link {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding: 20px 0 20px 0;
  }
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main__hero {
  display: flex;
  flex-direction: column;
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .main__hero {
    margin-bottom: 0;
  }
}

.main__promotion {
  display: flex;
  flex-direction: column;
  padding-bottom: 42px;
  margin-bottom: 31px;
}
@media (min-width: 768px) {
  .main__promotion {
    margin-bottom: 56px;
  }
}
@media (min-width: 1150px) {
  .main__promotion {
    padding-bottom: 0;
    margin-bottom: 68px;
  }
}

.main__about-us {
  display: flex;
  flex-direction: column;
  margin-bottom: 58px;
}
@media (min-width: 768px) {
  .main__about-us {
    margin-bottom: 82px;
  }
}
@media (min-width: 1150px) {
  .main__about-us {
    margin-bottom: 90px;
  }
}

.main__feedback {
  display: flex;
  flex-direction: column;
  padding-bottom: 52px;
  margin-bottom: 38px;
}
@media (min-width: 768px) {
  .main__feedback {
    padding-bottom: 132px;
    margin-bottom: 68px;
  }
}
@media (min-width: 1150px) {
  .main__feedback {
    padding-bottom: 0;
    margin-bottom: 71px;
  }
}

.main__contacts {
  display: flex;
  flex-direction: column;
  margin-bottom: 39px;
}
@media (min-width: 768px) {
  .main__contacts {
    margin-bottom: 57px;
  }
}
@media (min-width: 1150px) {
  .main__contacts {
    margin-bottom: 105px;
  }
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
  z-index: 1;
  background-color: #f9f9f9;
  background-image: url("../img/hero-background-mobile.jpg");
  background-repeat: no-repeat;
  background-position: 50% -15%;
  background-size: auto;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-background-mobile-2x.jpg");
  }
}
@media (min-width: 768px) {
  .hero {
    background-image: url("../img/hero-backgrount-tablet-desktop.jpg");
    background-size: auto;
    background-position: 50% 111%;
    padding-top: 20px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-backgrount-tablet-desktop-2x.jpg");
  }
}
@media (min-width: 1150px) {
  .hero {
    background-position: 50% 126%;
  }
}

.hero__title-container {
  width: 100%;
  padding: 73px 30px 120px 30px;
  max-width: 1150px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .hero__title-container {
    padding: 66px 50px 372px 50px;
  }
}

.hero__title {
  display: flex;
  text-align: center;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
  max-width: 540px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    font-size: 53px;
    line-height: 57px;
    max-width: 700px;
  }
}

.hero__list {
  padding: 0;
  list-style-type: none;
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .hero__list {
    padding-left: 50px;
    padding-right: 50px;
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.hero__item {
  background-color: #63d1bb;
  display: flex;
  align-items: center;
  position: relative;
  transition: margin 0.3s, background-color 0.3s;
}
@media (min-width: 768px) {
  .hero__item {
    width: 100%;
  }
}
.hero__item:hover, .hero__item:focus-within {
  margin-top: -5px;
  background-color: #59d5bc;
  transition: margin 0.3s, background-color 0.3s;
}
.hero__item:nth-child(2n) {
  background-color: #56c3b2;
}
.hero__item:nth-child(2n):hover, .hero__item:nth-child(2n):focus-within {
  background-color: #46c1ae;
  transition: margin 0.3s, background-color 0.3s;
}

.hero__quick-link-icon {
  color: #149080;
  opacity: 0.5;
  transform: translateY(4px);
}
.hero__quick-link-icon--interior {
  margin-right: -1px;
  margin-top: -5px;
}
.hero__quick-link-icon--lego-toy {
  margin-right: -10px;
  margin-top: -6px;
}

.hero__quick-link {
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  min-height: 103px;
  padding: 24px 36px 17px 30px;
  font-family: "Open Sans", "Arial", sans-serif;
  cursor: pointer;
  transition: padding 0.3s;
}
@media (min-width: 768px) {
  .hero__quick-link {
    padding: 25px 36px 34px 36px;
  }
}
@media (min-width: 1150px) {
  .hero__quick-link {
    padding: 25px 36px 34px 37px;
  }
}
.hero__quick-link:hover {
  padding-bottom: 36px;
  transition: padding 0.3s;
}
@media (min-width: 768px) {
  .hero__quick-link:hover {
    padding-bottom: 39px;
  }
}
.hero__quick-link:hover .hero__quick-link-icon {
  opacity: 0.7;
}
.hero__quick-link:active {
  padding-bottom: 36px;
  transition: padding 0.3s;
}
@media (min-width: 768px) {
  .hero__quick-link:active {
    padding-bottom: 39px;
  }
}
.hero__quick-link:active .hero__quick-link-icon {
  opacity: 0.3;
}
.hero__quick-link:focus {
  outline: transparent;
  box-shadow: 9px 9px 6px -5px rgba(0, 0, 0, 0.3);
}
.hero__quick-link:disabled {
  opacity: 0.3;
}

.hero__quick-link-text {
  margin: 0;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  max-width: 130px;
}
@media (min-width: 768px) {
  .hero__quick-link-text {
    font-size: 20px;
    line-height: 30px;
  }
}

.promotion {
  width: 100%;
  position: relative;
}
.promotion::after {
  content: "";
  position: absolute;
  background-image: url("../img/promotion-zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: 0 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 6px;
}
@media (min-width: 768px) {
  .promotion::before {
    content: "";
    position: absolute;
    background-image: url("../img/promotion-zigzag-fill.svg");
    background-repeat: repeat-x;
    background-position: 0 0;
    top: -24px;
    left: 0;
    width: 100vw;
    height: 24px;
    z-index: 1;
  }
  .promotion::after {
    background-image: url("../img/zigzag-line-tablet.svg");
    height: 17px;
  }
}
@media (min-width: 1150px) {
  .promotion::before {
    background-image: url("../img/zigzag-fill-desktop.svg");
    height: 34px;
    top: -34px;
  }
  .promotion::after {
    content: none;
  }
}

@media (min-width: 768px) {
  .promotion__container {
    position: relative;
  }
}
@media (min-width: 1150px) {
  .promotion__container {
    max-width: 1150px;
  }
  .promotion__container::after {
    background-image: url("../img/zigzag-line-desktop.svg");
    height: 23px;
    content: "";
