.modal__form-group-input[type=radio]:checked + .modal__form-group-control-label:active {
  border: 2px solid #62d1ba;
}

.modal__button {
  font-size: 17px;
  line-height: 16px;
  color: #ffffff;
  background-color: #63d1bb;
  outline: transparent;
  min-height: 52px;
  transition: background-color 0.3s;
}
.modal__button:hover {
  background-color: rgba(99, 209, 187, 0.6);
  transition: background-color 0.3s;
}
.modal__button:active, .modal__button:focus {
  background-color: rgba(99, 209, 187, 0.3);
  color: #ffffff;
  transition: background-color 0.3s;
}
.modal__button:disabled {
  opacity: 0.3;
  pointer-events: none;
}